@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

.container {
  background-color: bisque;
  height: 100vh;
  display: flex;
  align-items: center;
}

.wrapper {
  width: 296px;
  margin: 0 auto;
  padding: 8px;
  background-color: #fff;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 64px;
  text-align: center;
  font-size: 28px;
  background-color: #000;
  color: #fff;
  grid-gap: 4px;
  gap: 8px;
  border-radius: 16px;
  box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.5);
}

.screen {
  grid-column: 1 / 5;
  text-align: right;
  line-height: 64px;
  font-size: 44px;
  overflow: hidden;
}

.btn.zero {
  grid-column: 1 / 3;
  border-radius: 40px;
  text-align: left;
  padding-left: 24px;
}

.btn {
  background-color: #333;
  height: 100%;
  line-height: 64px;
  border-radius: 50%;
  cursor: pointer;
}

.btn.orange {
  background-color: orange;
}

.btn.gray {
  background-color: #999;
  color: #000;
}

.btn.gray:hover {
  background-color: #fff;
}

.btn.number:hover {
  background-color: #666;
}

.btn.operator {
  color: #fff;
  border: none;
  outline: none;
  font-size: 28px;
  background-color: orange;
}
.btn.orange:hover,
.btn.operator:hover {
  background-color: rgb(238, 223, 194);
  color: orange;
}

.btn.operator:focus {
  background-color: #fff;
  color: orange;
}
